/* src/app/header/header.scss */
* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}
body {
  font-family:
    "Inter",
    Arial,
    sans-serif;
  background: #f7f6f4;
  color: #212121;
  margin: 0;
  padding: 0;
}
.itau-header {
  height: 72px;
  display: flex;
  align-items: center;
  padding: 0 32px;
  background: #ff6200;
  color: #ffffff;
  box-shadow: 0 2px 8px rgba(33, 33, 33, 0.06);
  text-decoration: none;
}
.logo-area {
  display: flex;
  align-items: center;
  gap: 12px;
  padding: 12px;
}
.brand-title {
  font-family:
    "Inter",
    Arial,
    sans-serif;
  font-size: 24px;
  font-weight: 700;
  letter-spacing: 0.03em;
  color: #ffffff;
  text-decoration: none;
}
a,
a:active,
a:visited,
a:focus,
a:hover {
  text-decoration: none;
}
